<template>
  <div class="pageIndex">
    <Header v-show="showComponent" ></Header>  
   <div class="content">
        <Menu v-show="showComponent" class="leftMenu"></Menu>
        <div class="rightContent" :style="{'margin-left':maleft}">
            <router-view/>
        </div>
   </div>
    <!-- <el-row>
        <el-col :span="8" :offset="8" class="canvas">
            <el-button type="primary" icon="el-icon-edit" @click="showIndex">{{ext}}</el-button>
            <el-button type="primary" :plain="true" v-for=" (item,index) in btnArray " :key="item.val" @click="open(index)" >{{item.val}}</el-button>
        </el-col>
    </el-row> -->
  </div>
</template>
 
<script>
import Menu from '@/components/leftMenu';
import Header from '@/components/header';
export default {
  name: 'pageIndex',
  data () {
    return {
        showComponent:true,
        maleft:'200px',
        ext:'撰写',
        btnArray: [{
            val:'测试按钮'
        },{
            val:'默认按钮'
        }]
    }
  },
   watch:{
        menu:{
            handler:function (val) {
                let _that=this;
                if(val){
                     _that.maleft = '65px';
                }else{
                    _that.maleft = '200px';
                }
                console.log(menu,maleft)
            },
            deep:true
        }
        
    },
    mounted:function () {
        let _that=this;
        this.$router.push('/Canvas')
    },
    methods: {
        showIndex (){
            this.showComponent = true;
        },
        open (flag){
            let _that=this;
            switch (flag){
                case 0:
                    _that.$message('这是一条测试消息提示');
                    break;
                case 1:
                    _that.$message('这是一条默认消息提示');
                    break;
            }
          
        }
    },
    components:{
        Menu,
        Header
    },
    computed: {
        menu: function() {
        // 通过vuex的getters方法来获取state里面的数据
            return this.$store.getters.getMenuFn;
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .pageIndex{
        background: #fff;
    }
    .canvas{
        height: 400px;
        line-height: 400px;
        border: 1px solid rgb(192, 114, 114);
        background: #fff;
        border-radius: 5px;
    }
    .content{
        width: 100%;
        height: 100%;
    }
    .leftMenu {
        float: left;
    }
    .rightContent {
        height: 100%;
        background: #fff;
        margin-left: 65px;
    }
</style>



