import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
let store = new Vuex.Store({
    // 1. state
    state:{
        city:"城市名",
        subject:[],
        Menu : false,
    },

    // // 2. getters
    getters:{
        // 参数列表state指的是state数据
        getCityFn(state){
            return state.city;
        },
        getSubjectFn(state){
            return state.subject;
        },
        getMenuFn(state){
            return state.Menu;
        }
    },
    // 3. actions
    // 通常跟api接口打交道
    actions:{
        // 设置城市信息
        // 参数列表：{commit, state}
        // state指的是state数据
        // commit调用mutations的方法 
        // name就是调用此方法时要传的参数
        setCityName({commit,state}, name){
            // 跟后台打交道
            // 调用mutaions里面的方法
            commit("setCity", name);
        },
        setSubjectArr({commit,state}, obj){
            // 跟后台打交道
            // 调用mutaions里面的方法
            commit("setSubject", obj);
        },
        setmenuFlag({commit,state}, obj){
            console.log(obj)
            commit("setMenu", obj);
        }
    },
    // 4. mutations
    mutations:{
        setSubject(state,obj){
            state.subject.push(obj)
        },
        // state指的是state的数据
        // name传递过来的数据
        setCity(state, name){
            state.city = name;//将传参设置给state的city
        },
        setMenu(state, name){
            state.Menu = name;//将传参设置给state的city
        }
    }
});

export default store;


