import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld';
import Index from '@/page/index.vue';
import News from '@/page/list/new';
import Canvas from '@/page/list/canvas';
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },{
      path: '/index',
      name: 'Index',
      component: Index,
      children:[{
        path:'/News',
        name:'News',
        component : News
      },
      {
        path:'/Canvas',
        name:'Canvas',
        component : Canvas
      }]
    }
  ]
})



