<template>
    <div class="header">
        <div class="header-left" :style="{'width':headLeft}">
            <a href="javascript:;" class="logo"></a>
            <p class="logo-text" v-show="!menu">Bear</p>
        </div>
        <div class="header-right" :style="{'margin-left':maLeft}">
            <el-button type="primary" @click="openMenu" v-show="isCollapse" icon="el-icon-d-arrow-right"></el-button> 
            <el-button type="primary" @click="openMenu" v-show="!isCollapse" icon="el-icon-d-arrow-left"></el-button>    
        </div> 
    </div>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: false,
      headLeft: "160px",
      maLeft: "200px"
    };
  },
  watch: {
    menu: {
      handler: function(val) {
        let _that = this;
        if (val) {
          _that.headLeft = "25px";
          _that.maLeft = "65px";
        } else {
          _that.headLeft = "160px";
          _that.maLeft = "200px";
        }
      },
      deep: true
    }
  },
  methods: {
    openMenu() {
      this.isCollapse = !this.isCollapse;
      this.$store.dispatch("setmenuFlag", this.isCollapse);
    }
  },
  computed: {
    menu: function() {
      // 通过vuex的getters方法来获取state里面的数据
      return this.$store.getters.getMenuFn;
    }
  }
};
</script>

<style scoped>
    .header {
        width: 100%;
        height: 50px;
        text-align: left;
        position: fixed;
        top: 0;
    }
    .header-left{
        width: 65px;
        float: left;
        background: #423f3f;
        padding: 13px 20px;
    }
    .header-left>.logo{
        display: inline-block;
        width: 24px;
        height: 24px;
        background: url('../assets/imgs/icon.png')no-repeat;
        float: left;
        background-size: cover;
        background-position: 1px 2px;
    }
    .header-left>.logo-text{
        display: block;
        margin: 0;
        margin-left: 30px;
        color: #fff;
        height: 24px;
        line-height: 24px;
    }
    .header-right{
        background: #423f3f;
        height: 50px;
        line-height: 50px;
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
    }
    /* new Array(10000).fill('').map((_, index) => index + 1).filter(item => /0/.test(item)).reduce((count, item) => {
			return count + (String(item).match(/0/g) || []).length
		}, 0) */
</style>


